/** @format */
/** @format */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif !important;
}
img {
  max-width: 100%;
  height: auto;
}
.App {
  min-height: 100vh;
  width: 85vw;
  max-width: 1900px;
  margin: 0rem auto;
}
body {
  max-width: 100%;
  overflow-x: hidden;
}

body {
  background-color: #f6f6f6;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 90px;
  margin-top: 10px;
}
.navbar-menu-container {
  display: none;
}
.navbar-links-container a {
  margin-right: 3rem;
  text-decoration: none;
  color: #8b8b8b;
  font-size: 1.1rem;
}

.navbar-links-container a:hover {
  color: #ff64ae;
}

.navbar-links-container a:first-child {
  color: #41487f;
  font-weight: 500;
}
.primary-button {
  padding: 0.5rem 1.75rem;
  background-color: #ff64ae;
  outline: none;
  border: none;
  border-radius: 5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  transition: 0.2s;
  color: white;
}
.navbar-cart-icon {
  font-size: 1.15rem;
}
.primary-button:hover {
  background-color: #f253a0;
}
.nav-logo-container {
  margin-left: 5%;
}
.navbar-menu-container svg {
  font-size: 1.5rem;
  cursor: pointer;
}
.home-banner-container {
  position: relative;
  display: flex;
  padding-top: 3rem;
}
.home-bannerImage-container {
  position: absolute;
  top: -100px;
  left: -170px;
  z-index: -2;
}
.home-image-section {
  max-width: 600px;
  flex: 1;
}
.home-text-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: 5%;
}
.primary-heading {
  font-size: 48px;
  color: #091156;
  /* line-height: 5rem; */
  max-width: 600px;
  font-weight: 600;
  line-height: 60px;
}
.primary-heading.service {
  text-align: center;
}
.primary-text {
  font-size: 16px;
  max-width: 500px;
  color: #091156;
  margin: 1.5rem 0rem;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 10%;
}
.secondary-button {
  padding: 1rem 2.5rem;
  background-color: #ff64ae;
  outline: none;
  border: none;
  border-radius: 5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  color: white;
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.secondary-button svg {
  margin-left: 0.75rem;
  font-size: 1.5rem;
}
.secondary-button:hover {
  background-color: #f253a0;
}
.rectangle {
  width: 15px;
  height: 3px;
  background: #eeeeee;
  border-radius: 50px;
  transform: matrix(1, 0, 0, -1, 0, 0);
}
.rectangle + .rectangle {
  margin-left: 8px;
}

.rectangle.active {
  width: 26px;
  height: 8px;
  background: #414880;
  border-radius: 50px;
  transform: matrix(1, 0, 0, -1, 0, 0);
}
.home-container .rectangles {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
}
.about-section-container {
  /* margin-top: 20rem; */
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.about-section-container .about-section-text-container .primary-heading {
  font-size: 36px;
  font-weight: 600;
  line-height: 45px;
  letter-spacing: 0em;
  text-align: left;
  color: #091156;
}

.about-section-container .about-section-text-container .primary-text {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.1em;
  text-align: left;
  color: #8b8b8b;
}
.about-background-image-container {
  position: absolute;
  right: -150px;
  z-index: -2;
}
.about-section-image-container {
  flex: 0.9;
  margin-right: 1rem;
}
.about-section-text-container {
  flex: 1;
  justify-content: center;
  display: flex;
  flex-direction: column;
}
.primary-subheading {
  font-weight: 600;
  color: #ff64ae;
  font-size: 16px;
}
.about-buttons-container {
  margin-top: 2rem;
  display: flex;
}
.watch-video-button {
  margin-left: 2rem;
  background-color: transparent;
  outline: none;
  border: none;
  border-radius: 5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  color: #484848;
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.watch-video-button svg {
  font-size: 3rem;
  margin-right: 1rem;

  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0.1em;
  text-align: left;
}
.watch-video-button span {
  color: #091156;
}

.contact-container {
  display: flex;
}
/* .contact-image-section {
  margin-right: 5%;
}
.contact-content {
  margin-left: 10%;
} */
.contact-page-wrapper h1 {
  /* max-width: 900px !important; */
}
.contact-page-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* flex-direction: column; */
  margin: 10rem 0rem;
}
.contact-page-wrapper .contact-background {
  position: absolute;
  left: 0px;
  z-index: -2;
}
.contact-form-container {
  background-color: white;
  max-width: 700px;
  width: 100%;
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  border-radius: 5rem;
}
.contact-form-container input {
  flex: 1;
  height: 100%;
  border: none;
  outline: none;
  font-size: 1.3rem;
  padding: 0.5rem 1rem;
}
